/* ===================== Imports ==================== */
import React from 'react';  // import React to create React component
import './Loader.css';  // import the CSS file
/* ================================================== */

// Create a new class 'Loader' which acts as a React component
class Loader extends React.Component {
    // Create a constructor which construct the local variables 
    constructor() {
        super();
        this.state = {
            showingLoader: true, // Check if the if the loader element is loaded
            loaderBack: false, // Check if the z-index of the loader element is -1
            loader: (<span class="loader"><span class="loader-inner"></span></span>), // Define a <span> element holding our classNames
            key: 0  // Define a key number for the React to distinguish between elements
        };
    }

    // This function will start working once the object is rendered in the page
    componentDidMount() {
        // setTimeout() is used to delay the function inside it.
        setTimeout(() => {
            // Set the state of the variable inside
            this.setState({
                showingLoader: false, // Change showingLoader from true to false
            });
        }, 2000); // Delay by 2 sec

        // setTimeout() is used to delay the function inside it.
        setTimeout(() => {
            // Set the state of the variable inside
            this.setState({
                loaderBack: true, // Change loaderBack from false to true
            });
        }, 2450); // Delay by 2.45 sec
    }

    // This function is used to render the components defined below
    render() {
        // return JSX items
        return (
            <div 
                key={this.state.key} 
                id="loader-wrapper" 
                style={{
                    opacity: (this.state.showingLoader ? "100%" : "0%"),
                    transition: "opacity 0.5s ease-out", 
                    zIndex: (this.state.loaderBack ? "-1" : "1"),
                    }}
                >
                {this.state.loader}
            </div>
            
        );
    }
}

// export 'Loader' class as the default export of './Loader.js'
export default Loader;