/* ===================== Imports ==================== */
import React from 'react'; // import React to create React component
import ReactScrollWheelHandler from "react-scroll-wheel-handler"; // import Mouse Wheel Scroll action handler
import './App.css'; // import the CSS file
import greenFlag from './static/img/greenFlag.gif'; // import gif image from the 'src' folder
import background from './static/img/home.gif' // import gif image from the 'src' folder
/* ================================================== */

// Create a class that represents the links in the home page scroll bar
// an object of link takes an id (int) to diferenciate, a className (str) to define, a link (str) to hyperlink, a imgSource (str) to link to an image, and a text (str) to display
class link {
  constructor(id, className, link, imgSource, text) {
    this.id = id
    this.className = className
    this.link = link
    this.imgSource = imgSource
    this.text = text
  }
}

// Create an array and populate it with objects of link
var links = [
  new link(0, "section Contact", "#", greenFlag, "CONTACT"),
  new link(1, "section Home", "#", greenFlag, "HOME"),
  new link(2, "section AboutUs", "#", greenFlag, "ABOUT US"),
  new link(3, "section Panaromas", "https://wwi-underground.org/panoramas", greenFlag, "PANAROMAS"),
  new link(4, "section UntoldStories", "#", greenFlag, "UNTOLD STORIES"),
  new link(5, "section DesktopViewer", "https://wwi-underground.org/downloads/WWI%20Underground%20Desktop%20Viewer%20[2019-12-11].zip", greenFlag, "DESKTOP VIEWER"),
];

// This function gets all the link objects from the array links and creates <li> elements filled with the information from all the link objects
// This function does this two times to have the both sides of the scrollable menu
function createList() {

  var list = []   // Creatte a list to fill with <li> elements
  var keyNum = 0  // Assign a key nunber to each individual <li> element
  
  // Initial for loop is to repeat twice to fill the back side of the looping scroll
  for (var j = 0; j < 2; j++) {
    // This for loop is used for filling the list array with <li> elements created using information from link objects
    for (var i = 0; i < links.length; i++) {

      // this part calculates the key number for elements.
      if (j === 0) {
        keyNum = links[i].id;
      } else {
        keyNum = links[i].id + links.length;
      }

      // Give different indentation to each <li> according to their keyNumber (or position in the looping menu)
      if (i % 2 === 0) {
        list.push(<li key={keyNum} className={links[i].className} style={{ paddingLeft: "(-" + links[i].text.length + "vw)" }}><a href={links[i].link}><span>{links[i].text}<img src={links[i].imgSource} alt={"image for " + links[i].className + " page"} /></span></a></li>)
      } else {
        list.push(<li key={keyNum} className={links[i].className} style={{ paddingLeft: "(" + links[i].text.length + "vw)" }}><a href={links[i].link}><span><img src={links[i].imgSource} alt={"image for " + links[i].className + " page"} style={{ transform: "rotateY(180deg)" }} />{links[i].text}</span></a></li>)
      }
    }
  }

  // return the list populated by <li> elements
  return (
    list
  );
}

// Create a new class 'App' which acts as a React component
class App extends React.Component {
  // define local variables
  state = {
    currentScrollIndex: -10, // Store the current scroll index
    inverseScrollIndex: 0, // Store the oposite value of the current scroll index
    list: createList(), // populate the local variable with the return argument of createList() function
  };

  // This function is used when the user scrolls down
  nextIndex = () => {
    var { list, currentScrollIndex, inverseScrollIndex } = this.state;  // Call the local variables

    console.log("Next") // Just to check which action have been taken bny the user

    list.unshift(list.pop()); // Remove the last element from the 'list' array and insert it at the start
    // To set the local variables
    this.setState({
      currentScrollIndex: currentScrollIndex + 10, // add 10 to the currentScrollIndex
      inverseScrollIndex: inverseScrollIndex - 10, // decrease 10 from the inverseScrollIndex
    });
  };

  // This function is used when the user scrolls up
  prevIndex = () => {
    var { list, currentScrollIndex, inverseScrollIndex } = this.state;  // Call the local variables

    console.log("Prev") // Just to check which action have been taken bny the user

    list.push(list.shift()); // Remove the first element from the 'list' array and insert it at the end
    // To set the local variables
    this.setState({
      currentScrollIndex: currentScrollIndex - 10, // decrease 10 from the currentScrollIndex
      inverseScrollIndex: inverseScrollIndex + 10, // add 10 to the inverseScrollIndex
    });
  };

  // This function is used to render the components defined below
  render() {
    var { list, currentScrollIndex, inverseScrollIndex } = this.state;  // Call the local variables
    // return JSX items

    // Create a different set up for mobile devices
    if (window.innerWidth <= 1024) {
      // return JSX items
      return (
        <>
          <img
            src={background}
            alt="background"
            style={{
              opacity: "100%",
              position: "absolute",
              width: "100%",
              height: "55%",
              top: "0",
              left: "0",
              padding: "0",
              margin: "0",
              filter: "brightness(50%)",
            }}
          />
          <div className="container">
            <ul className="carousel">
              {list}
            </ul>
          </div>
        </>
      );
    } else {
      // return JSX items
      return (
        <>
          <img
            src={background}
            alt="background"
            style={{
              opacity: "100%",
              position: "absolute",
              width: "100%",
              height: "100%",
              top: "14%",
              left: "0",
              padding: "0",
              margin: "0",
              filter: "brightness(50%)",
            }}
          />
          <ReactScrollWheelHandler
            upHandler={this.prevIndex}
            downHandler={this.nextIndex}
            wheelConfig={[15,75,1]}
            style={{
              width: "100%",
              height: "100vh",
              transform: "translateY(" + currentScrollIndex + "vh)",
              transition: "transform 0.7s",
            }}
          >
            <div className="container">
              <ul className="carousel" style={{ transform: "translateY(" + inverseScrollIndex + "vh)", }}>
                {list}
              </ul>
            </div>
          </ReactScrollWheelHandler>
        </>
      );
    }
    
  }
}

// export 'App' class as the default export of './App.js'
export default App;